import React, { useEffect, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';


import styles from './stylesCase.css';
import CaseCard from './CaseCard';

// import required modules



function CaseStudiesBlock() {

  const [isLargeScreen, setIsLargeScreen] = useState(false);


    useEffect(() => {
    
      const checkScreenSize = () => {
        setIsLargeScreen(window.innerWidth <= 768);
      };

    
      checkScreenSize();

    
      window.addEventListener('resize', checkScreenSize);

      
      return () => {
        window.removeEventListener('resize', checkScreenSize);
      };
    }, []);

    const cardInfo = [
      {
        info:'For a local restaurant, we implemented a targeted PPC campaign that resulted in a 50% increase in website traffic and a 25% increase in sales.'
      },
      {
        info:'For a B2B software company, we developed an SEO strategy that resulted in a first page ranking for key keywords and a 200% increase in organic traffic.'
      },
      {
        info:'For a national retail chain, we created a social media marketing campaign that increased followers by 25% and generated a 20% increase in online sales.'
      },
      
    ]

  return (
    <div data-aos="fade-in" className=' md:inline-flex px-5 md:px-[100px] md:flex-col md:items-start gap-[10px] md:w-[1440px]'>
      {isLargeScreen ? 
      
      <Swiper
        slidesPerView={1.2}  
        spaceBetween={30}
        className={'mySecondSwiper'}
      >
        {cardInfo.map(el => (
          <SwiperSlide className='oneslide'>
            <CaseCard text={el.info}/>
          </SwiperSlide>
        ))}
        

      </Swiper>
    
      :
      <div className='flex py-[70px] px-[60px] items-start gap-16 rounded-[45px] bg-black'>
        <div className='flex flex-col items-start gap-5'>
          <div className=' text-lg leading-normal text-white'>For a local restaurant, we implemented a targeted PPC campaign that resulted in a 50% increase in website traffic and a 25% increase in sales.</div>
          <a className='flex items-center gap-[15px]'>
            <div className=' text-xl font-normal text-green'>Learn more</div>
            <svg xmlns="http://www.w3.org/2000/svg" width="21" height="20" viewBox="0 0 21 20" fill="none">
  <path d="M1.25 13.701C0.532561 14.1152 0.286748 15.0326 0.700962 15.75C1.11518 16.4674 2.03256 16.7133 2.75 16.299L1.25 13.701ZM20.7694 5.38823C20.9838 4.58803 20.5089 3.76552 19.7087 3.55111L6.66874 0.0570541C5.86854 -0.157359 5.04603 0.317515 4.83162 1.11771C4.61721 1.91791 5.09208 2.74042 5.89228 2.95483L17.4834 6.06066L14.3776 17.6518C14.1631 18.452 14.638 19.2745 15.4382 19.4889C16.2384 19.7033 17.0609 19.2284 17.2753 18.4282L20.7694 5.38823ZM2.75 16.299L20.0705 6.29904L18.5705 3.70096L1.25 13.701L2.75 16.299Z" fill="#B9FF66"/>
            </svg>
          </a>
        </div>
        <span className=' w-[1px] h-[186px] bg-white'></span>
        <div className='flex flex-col items-start gap-5'>
          <div  className=' text-lg leading-normal text-white'>For a B2B software company, we developed an SEO strategy that resulted in a first page ranking for key keywords and a 200% increase in organic traffic.</div>
          <a className='flex items-center gap-[15px]'>
            <div className=' text-xl font-normal text-green'>Learn more</div>
            <svg xmlns="http://www.w3.org/2000/svg" width="21" height="20" viewBox="0 0 21 20" fill="none">
  <path d="M1.25 13.701C0.532561 14.1152 0.286748 15.0326 0.700962 15.75C1.11518 16.4674 2.03256 16.7133 2.75 16.299L1.25 13.701ZM20.7694 5.38823C20.9838 4.58803 20.5089 3.76552 19.7087 3.55111L6.66874 0.0570541C5.86854 -0.157359 5.04603 0.317515 4.83162 1.11771C4.61721 1.91791 5.09208 2.74042 5.89228 2.95483L17.4834 6.06066L14.3776 17.6518C14.1631 18.452 14.638 19.2745 15.4382 19.4889C16.2384 19.7033 17.0609 19.2284 17.2753 18.4282L20.7694 5.38823ZM2.75 16.299L20.0705 6.29904L18.5705 3.70096L1.25 13.701L2.75 16.299Z" fill="#B9FF66"/>
            </svg>
          </a>
        </div>
        <span className=' w-[1px] h-[186px] bg-white' ></span>
        <div className='flex flex-col items-start gap-5'>
          <div  className=' text-lg leading-normal text-white'>For a national retail chain, we created a social media marketing campaign that increased followers by 25% and generated a 20% increase in online sales.</div>
          <a className='flex items-center gap-[15px]'>
            <div className=' text-xl font-normal text-green'>Learn more</div>
            <svg xmlns="http://www.w3.org/2000/svg" width="21" height="20" viewBox="0 0 21 20" fill="none">
  <path d="M1.25 13.701C0.532561 14.1152 0.286748 15.0326 0.700962 15.75C1.11518 16.4674 2.03256 16.7133 2.75 16.299L1.25 13.701ZM20.7694 5.38823C20.9838 4.58803 20.5089 3.76552 19.7087 3.55111L6.66874 0.0570541C5.86854 -0.157359 5.04603 0.317515 4.83162 1.11771C4.61721 1.91791 5.09208 2.74042 5.89228 2.95483L17.4834 6.06066L14.3776 17.6518C14.1631 18.452 14.638 19.2745 15.4382 19.4889C16.2384 19.7033 17.0609 19.2284 17.2753 18.4282L20.7694 5.38823ZM2.75 16.299L20.0705 6.29904L18.5705 3.70096L1.25 13.701L2.75 16.299Z" fill="#B9FF66"/>
            </svg>
          </a>
        </div>
      </div>
      
      }
    </div>
  );
}

export default CaseStudiesBlock;
